<template>
  <div>
    <mt-header title="个人信息填写">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    {{msg}}
    <mt-field label="姓名" placeholder="请输入姓名" v-model="username"></mt-field>
    <mt-field label="学号" placeholder="请输入学号" v-model="idnumber"></mt-field>
    <mt-field label="手机号" placeholder="请输入手机号" v-model="phonenumber"></mt-field>
    <mt-field label="邮箱" placeholder="请输入邮箱" v-model="mailaddress"></mt-field>
    <button @click="onClick" class="mint-button mint-button--primary mint-button--large">确认</button>
    <!-- <button @click="onCancel" class="mint-button mint-button--primary mint-button--large">取消报名</button> -->
  </div>
</template>

<script>
import { MessageBox } from "mint-ui";
import { modifyUserinfo, userRegister } from "../../request/api"; // 导入我们的api接口
export default {
  name: "RegisterPage",
  data() {
    return {
      msg: "Welcome to Register Page",
      username: "",
      idnumber: "",
      phonenumber: "",
      mailaddress: "",
      openId: "",
      sheetVisible: false,
      sheetactions: [
        {
          name: "查看报名人员",
          method: this.getmemberlist // 调用methods中的函数
        }
      ]
    };
  },
  created: function() {
    console.log("register created");
    this.openId = this.getCookie("openId");
  },
  methods: {
    onClick: function() {
      console.log("onClick");
      console.log(
        this.username,
        this.idnumber,
        this.phonenumber,
        this.mailaddress,
        this.openId
      );
      let params = { openId: this.openId };
      MessageBox.confirm("确定注册用户?").then(action => {
        console.log("register request", params);
        userRegister(params).then(res => {
          if (res.message == "success") {
            params.name = this.username;
            params.ecnuId = this.idnumber;
            params.phoneNumber = this.phonenumber;
            params.email = this.mailaddress;
            console.log(params);
            modifyUserinfo(params).then(res => {
              // console.log(res)
              if ((res.message = "success")) {
                this.$router.go(-1);
              }
            });
          }
        });
      });
    },
    setCookie: function(cname, cvalue, extime) {
      // console.log(cname, cvalue, extime);
      this.$cookies.set(cname, cvalue, extime);
    },
    getCookie: function(cname) {
      var ret = this.$cookies.get(cname);
      // console.log(cname, ret);
      return ret;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}

/* a {
  color: #42b983;
} */
</style>



